* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: “Alata”, “Sofia pro” , “Nunito”, sans-serif;
}
 
.container {
  width: 100%;
  min-height: 100vh;
  background-image: linear-gradient(rgb(9, 0, 77, 0.65), rgba(9, 0, 77, 0.65));
  background-image: url("https://static.vecteezy.com/system/resources/previews/033/379/576/large_2x/wallpaper-the-sky-mountains-water-lake-switzerland-lake-the-alps-ai-generated-free-photo.jpg?wallpaper,landscape");
  background-size: cover;
  background-position: center;
  padding: 10px 8%;
}
 
nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}
 nav h1{
  color: #fff;
  font-size: 40px;
 }
.logo {
  width: 180px;
  cursor: pointer;
}
 
nav ul {
  list-style: none;
  width: 100%;
  text-align: right;
  padding-right: 60px;
}
 
nav ul li {
  display: inline-block;
  margin: 10px 20px;
}
 
nav ul li a {
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  font-weight: 400;
}
 
.btn {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border: 0;
  outline: 0;
  border-radius: 5px;
  background: #9d59b8;
  color: #ffffff;
  font-weight: 500;
  cursor: pointer;
}
 
.btn img {
  width: 20px;
  margin-right: 10px;
}
 
.content {
  margin-top: 14%;
  color: #fff;
  max-width: 620px;
}
 
.content h1 {
  font-size: 70px;
  font-weight: 800;
  line-height: 85px;
  margin-bottom: 25px;
}
 
.content form {
  display: flex;
  align-items: center;
  background: #fff;
  border-radius: 5px;
  padding: 10px;
  margin-top: 30px;
}
 
.content form input {
  border: 0;
  outline: 0;
  width: 100%;
  font-size: 16px;
  padding-left: 10px;
}
 
.content form .btn {
  font-size: 15px;
  padding: 10px 30px;
}
